.crypto-header__payment .button-main span {
  text-transform: none;
  font-weight: 700; }

.crypto-header__mobile-menu .button-main {
  border-radius: 4px;
  margin-bottom: 10px; }

.crypto-header__mobile-menu .button-main span {
  text-transform: none;
  font-size: 14px; }

@media (max-width: 959px) {
