/*Animation*/
@keyframes logoAnimation {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.5; }
  100% {
    opacity: 1; } }

@-webkit-keyframes navLinkAnimation {
  0% {
    width: 10px;
    opacity: 0; }
  25% {
    width: 10px;
    opacity: 1; }
  50% {
    width: 10px;
    opacity: 0; }
  75% {
    width: 10px;
    opacity: 1; }
  100% {
    width: 100%;
    opacity: 1; } }

@-moz-keyframes navLinkAnimation {
  0% {
    width: 10px;
    opacity: 0; }
  25% {
    width: 10px;
    opacity: 1; }
  50% {
    width: 10px;
    opacity: 0; }
  75% {
    width: 10px;
    opacity: 1; }
  100% {
    width: 100%;
    opacity: 1; } }

@-ms-keyframes navLinkAnimation {
  0% {
    width: 10px;
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; }
  25% {
    width: 10px;
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"; }
  50% {
    width: 10px;
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; }
  75% {
    width: 10px;
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"; }
  100% {
    width: 100%;
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"; } }

@keyframes navLinkAnimation {
  0% {
    width: 10px;
    opacity: 0; }
  25% {
    width: 10px;
    opacity: 1; }
  50% {
    width: 10px;
    opacity: 0; }
  75% {
    width: 10px;
    opacity: 1; }
  100% {
    width: 100%;
    opacity: 1; } }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.5);
    opacity: 0; } }

@-moz-keyframes pulse {
  0% {
    -moz-transform: scale(0);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -moz-transform: scale(1.5);
    opacity: 0; } }

@-ms-keyframes pulse {
  0% {
    -ms-transform: scale(0);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; }
  50% {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"; }
  100% {
    opacity: 0;
    -ms-transform: scale(1.5);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; } }

@-o-keyframes pulse {
  0% {
    -o-transform: scale(0);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -o-transform: scale(1.5);
    opacity: 0; } }

@keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    transform: scale(1.5);
    opacity: 0; } }

