/**
 * reg form styles
 */

.crypLogin__form-wrapper {
  position: relative;
  z-index: 1;
  padding: 40px;
  background-color: #fff;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.3);
}

.main-form {
  margin: 40px 0;
}

.nav-tabs {
  display: flex;
  justify-content: space-between;
  border: none;
}

.nav-tabs .nav-item {
  width: calc(50% - 5px);
  text-align: center;
  text-transform: uppercase;
}

.nav-tabs .crypLogin__tab {
  position: relative;
  top: -1px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 15px 0;
  border-radius: 0;
  border: none;
}

.crypLogin__tab-icon {
  display: block;
  width: 12px;
  height: 17px;
  margin-right: 6px;
  background-size: cover;
}

.crypLogin__tab:hover {
  color: white;
}

.nav-tabs .nav-item.show .crypLogin__tab,
.nav-tabs .crypLogin__tab.active {
  z-index: 2;
  box-shadow: 0 7px 0 #fff, 0 1px 6px rgba(0,0,0, .3);
}

.nav-tabs .nav-item.show .crypLogin__tab::after,
.nav-tabs .crypLogin__tab.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  border-bottom: 1px solid #dee2e6;
}

.nav-tabs .crypLogin__tab_login {
  color: white;
  background-color: #42d67e;
}

.nav-tabs .crypLogin__tab_login .crypLogin__tab-icon {
  background-image: url(../img/CCCpoint_login-register-weak.png);
  background-position: left;
}

.nav-tabs .crypLogin__tab_login.active {
  color: #42d67e;
  background-color: white;
}

.nav-tabs .crypLogin__tab_login.active .crypLogin__tab-icon {
  background-image: url(../img/CCCpoint_login-login.png);
  background-position: left;
}

.nav-tabs .crypLogin__tab_reg {
  color: white;
  background-color: #0dd0fe;
}

.nav-tabs .crypLogin__tab_reg .crypLogin__tab-icon {
  background-image: url(../img/CCCpoint_login-login.png);
  background-position: right;
}

.nav-tabs .crypLogin__tab_reg.active {
  color: #0dd0fe;
  background-color: white;
}

.nav-tabs .crypLogin__tab_reg.active .crypLogin__tab-icon {
  background-image: url(../img/CCCpoint_login-register-weak.png);
  background-position: right;
}

label {
  color: #bec2c3;
  vertical-align: middle;
}

.form-control {
  padding: 10px;
  border-radius: 0;
  background-color: #f5f5f5;
}

.crypLogin__forgotPass {
  display: flex;
  justify-content: flex-end;
}

.forgotPass-link {
  color: #2bbfd8;
  text-decoration: underline;
}

.forgotPass-link:hover,
.forgotPass-link:active,
.forgotPass-link:focus {
  text-decoration: none;
}

@media (max-width: 992px) {
  .input-reg-password {
    margin-bottom: 14px;
  }
}
/* kv-password */

.form-group {
position: relative;
}

.kv-strength-container td {
width: 100%
}

td.kv-meter-container {
position: absolute;
top: 85px;
left: 0;
width: 460px;
}

.kv-scorebar-border,
.kv-score {
width: 100% !important;
margin: 0;
}

.kv-scorebar {
width: 99% !important;
background-size: 250% !important;
}

.crypReg__agreements {
margin-top: 45px;
}

.form-button {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.form-check-label {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

/* checkboxes */
.form-check-label + label {
  position: relative;
  padding: 20px 0 0 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.form-check-label + label:before {
  content: '';
  position: absolute;
  left: 0;
  width: 20px;
  height: 20px;
  background-color: #f5f5f5;
  box-shadow: inset 1px 1px #e4e4e4;
  border: 1px solid #f1f1f1;
}

.form-check-label + label:after {
  content: '';
  position: absolute;
  background-image: url(../img/ok.png);
  height: 20px;
  width: 20px;
  left: 0;
  background-size: 100%;
  background-repeat: no-repeat;
  left: 5px;
  top: 18px;
  opacity: 0;
}

.form-check-label:checked + label:after {
  opacity: 1;
}

.form-check-label:checked + label {
  color: #000;
}

.captcha img {
  max-width: 300px;
  width: 100%;
}

.weak{
  color:#ff3e4a;
}
.good{
  color:#ffb24b;
}
.strong{
  color:#48d47b;
}

.check-pass {
  padding: 20px 0;
}

#pwdMeter {
  text-transform: uppercase;
  font-weight: bold;
}

.check-pass-spans {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
}

.check-pass-span {
  display: block;
  width: 30px;
  height: 8px;
  background-color: #f5f5f5;
  box-shadow: inset 1px 1px #e4e4e4;
  border: 1px solid #f1f1f1;
  margin: 5px;
}

.check-pass-title {
  color: #bfc2c4;
  font-weight: bold;
}

.weakspan{
  background-color:#ff3e4a;
}
.goodspan{
  background-color:#ffb24b;
}
.strongspan{
  background-color:#48d47b;
}
